// 뷰
<template>
  <div class="vuegram-post">
    <div class="header level">
      <div class="level-left">
        <figure class="image is-32x32">
          <img :src="post.userImage">
        </figure>
        <span class="username">{{post.username}}</span>
      </div>
    </div>

    <!-- 이미지 들어가는 부분 -->
    <div class="image-container" :class="post.filter" :style="{backgroundImage:'url('+post.postImage+')'}" @dblclick="like"></div>
    <!-- 글씨 들어가는 부분 -->
    <div class="content">
      <div class="heart">
        <i class="far fa-heart fa-lg" :class="{'fas':this.post.hasBeenLiked}" @click="like"></i> <!-- 하트가 눌러졌을때 fas란 클래스가 추가된다 -->
      </div>
      <p class="likes">{{post.likes}}</p> <!-- {{}}콧수엽 - 데이터바인딩(데이터값을 화면에 내보내는것) -->
      <p class="caption">
        <span>{{post.username}}</span>
        {{post.caption}}
      </p>
    </div>
  </div>
</template>
<script>
export default {
  name: 'VuegramPost',
  props: {
    post: Object // post는 Object(객체)
  },
  methods: {
    like () {
      // 삼합연산자  조건 ? 참:거짓  참이면 앞에있는거 실행, 거짓이면 뒤에 있는 거 실행

      // 좋아요가 눌려있을때(true) 누르면 --,없을때는(false) ++
      this.post.hasBeenLiked ? this.post.likes-- : this.post.likes++
      // 현재상태의 반대를 저장해라. 하트가 없을땐 false, 있을땐 true
      this.post.hasBeenLiked = !this.post.hasBeenLiked
    }
  }
}
</script>
<style lang="scss" src="../styles/vuegram-post.scss"></style>
