<template>
  <div class="filter-type">
    <p>{{filter.name}}</p>
    <!-- 클래스에 filter.name을 적용해주면 그 필터에 맞게 이미지가 적용된다. -->
    <div class="img" :class="filter.name" :style="{backgroundImage: 'url('+image+')'}" @click="selectFilter"></div>
        <!-- 클릭했을때 selectFilter라는 함수호출. -->
  </div>
</template>
<script>
import EventBus from '../event-bus.js'

export default {
  name: 'filterType',
  props: {
    filter: Object,
    image: String
  },
  methods: {
    selectFilter () { // 함수 선언
      // 선택한 필터 이름을 전달
      EventBus.$emit('filter-selected', { filter: this.filter.name })
      // 상위에 전달하는 법 this.$emit("이벤트이름",{전달하는 인자값});
      // 간단하게 이벤트 버스로 전달하는 법 EventBus.$emit("이벤트이름",{전달하는 값});
    }
  }
}
</script>
<style lang="scss" src="../styles/filter-type.scss"></style>
