<template>
  <div id='app'>
    <div class='app-phone'>
      <div class='phone-header'>
        <img src='./assets/vue_gram_logo_cp.png' alt='logo'>
        <!-- cancle을 클릭했을때 goToHome이라는 함수로 가라 -->
        <a class='cancel-cta' v-if='step===2 || step===3' @click='goToHome, step--'>Cancel</a>
        <a class='next-cta' v-if='step===2' @click='step++'>Next</a>
        <a class='next-cta' v-if='step===3' @click='sharePost'>Share</a>
      </div>
      <phone-body :step='step' :posts='posts' :filters='filters' :image='image' :selectedFilter='selectedFilter' v-model='caption'> <!-- v-model의 값은 caption으로 하자. -->
      </phone-body>
      <div class='phone-footer'>
        <div class='home-cta' @click='goToHome'>
          <i class='fas fa-home'></i>
        </div>
        <div class='upload-cta'>
          <input type='file' name='file' id='file' class='inputfile' @change='uploadImage' :disabled='step!==1'>
                <!-- @change='uploadImage' change라는 이벤트가 발생하면 uploadImage 함수 호출해라 @change - 내가 무엇을 선택하는지에 따라 바뀌는 것 :disabled='step!==1' step값이 1이 아닌경우 비활성화되라 -->
          <label for='file'>
            <i class='fas fa-plus-square fa-lg'></i>
          </label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PhoneBody from './components/PhoneBody.vue'
import posts from './data/posts.js'
import filters from './data/filters.js'

import EventBus from './event-bus.js'

export default {
  name: 'App',
  data () {
    return {
      step: 1,
      posts,
      filters,
      image: '', // 이미지를 선택했을때 그 이미지 값이 저장되는 곳
      selectedFilter: '',
      caption: ''
    }
  },
  created () {
    // 이벤트를 받을때
    // v-on:이벤트이름='함수이름'
    // 직접 항수를 적용할때는 아래와 같이
    // EventBus.$on('이벤트 이름', 매개변수 => 실행문)
    EventBus.$on('filter-selected', evt => {
      this.selectedFilter = evt.filter
    })
  },
  methods: { // 함수
    // cancle버튼 눌렀을때 실행되는 함수
    goToHome () {
      // 현재 컴포넌트의 이미지, 필터적용, 캡션 사라지고 step은 1로 가라.
      this.image = ''
      this.selectedFilter = ''
      this.caption = ''
      this.step = 1
    },

    // step 2에서 share버튼 눌렀을때
    sharePost () {
      const post = {
        username: 'goeun_y', // 고정
        userImage: 'https://s3-us-west-2.amazonaws.com/s.cdpn.io/1211695/me_3.jpg', // 고정
        postImage: this.image, // 선택한 이미지
        likes: 0,
        caption: this.caption,
        filter: this.selectedFilter
      }
      // .unshift() 배열의 맨위에 첫번째로 넣는것
      this.posts.unshift(post)
      this.goToHome()
    },

    uploadImage (evt) {
      // files라는 상수에다가 클릭한 이벤트를 저장해라.
      const files = evt.target.files
      // return이 실행되려면 ()값이 true여야한다.
      // files.length가 false일때는 파일의 값이 없을때이다.
      if (!files.length) return

      // 파일의 갯수가 하나 이상일때 아래의 실행문이 실행돼라

      // FileReader() 파일을 읽어주는 내장함수
      const reader = new FileReader()
      reader.readAsDataURL(files[0])
      reader.onload = evt => {
        // 이벤트 객체의 타겟이 되는 결과값을 이미지에 넣어줘라
        this.image = evt.target.result
        this.step = 2
      }
      document.querySelector('#file').value = ''
    }
  },
  components: {
    'phone-body': PhoneBody
  }
}
</script>

<style lang='scss' src='./styles/app.scss'>
</style>
