<template>
  <div class="phone-body"> <!-- 총 3가지 종류의 화면이 나온다. -->
    <div v-if="step===1" class="feed" v-dragscroll.y>
      <!-- posts라는 배열안에 하나의 객체는 post라 한다. -->
      <!-- v-for="(post, index) in posts" - posts안에 객체 하나하나를 post라 해라. -->
      <vuegram-post v-for="(post, index) in posts" :post="post" :key="index"></vuegram-post>
    </div>

    <div v-if="step===2">
      <!-- 내가 선택한 이미지가 나오는 화면 -->
      <div class="selected-image"
          :class="selectedFilter"
          :style="{backgroundImage: 'url('+image+')'}">
      </div>

      <!-- 필터들이 모여있는 컨테이너 -->
      <div class="filter-container" v-dragscroll.x>
        <filter-type v-for="(filter,index) in filters" :filter="filter" :image="image" :key="index">
        </filter-type>
      </div>
    </div>

    <!-- 텍스트 입력 -->
    <div v-if="step===3">
      <div class="selected-image"
          :class="selectedFilter"
          :style="{backgroundImage: 'url('+image+')'}">
      </div>
      <div class="caption-container">
        <!-- 여러줄 입력 -->
        <textarea class="caption-input" placeholder="Write a caption" type="text" :value="value" @input="$emit('input',$event.target.value)">
                <!-- @input="$emit('input',$event.target.value)" - 부모에게 보내는 전달 공식! -->
                <!-- input이란 이벤트가 발생하면(글씨를 입력하면) 값을 화면에 입력 부모에게 전달하려면 $emit을 사용하여 보내주고 부모는 v-model을 통해 전달 받는다!!!-->
        </textarea>
      </div>
    </div>
  </div>
</template>
<script>
import VuegramPost from './VuegramPost.vue' // step1
import FilterType from './FilterType.vue' // step2

export default {
  name: 'PhoneBody',
  props: {
    step: Number,
    posts: Array,
    selectedFilter: String,
    image: String,
    filters: Array,
    value: String
  },
  components: {
    // VuegramPost객체 가져와서 컴포넌트이름을 vuegram-post로 하겠다.
    'vuegram-post': VuegramPost,
    'filter-type': FilterType
  }
}
</script>
<style lang="scss" src="../styles/phone-body.scss"></style>
